import Vue from 'vue'
import Router from 'vue-router'
//import Inicio from './components/Inicio.vue'
const Inicio = () => Promise.resolve(
    import ('./components/Inicio.vue'));
//import Sobremi from './components/Sobremi.vue'
const Sobremi = () => Promise.resolve(
    import ('./components/Sobremi.vue'));
//import Contacto from './components/Contacto.vue'
const Contacto = () => Promise.resolve(
    import ('./components/Contacto.vue'));
//import Post from './components/Post.vue'
const Post = () => Promise.resolve(
    import ('./components/Post.vue'));
//import Articulo from './components/Articulo.vue'
const Articulo = () => Promise.resolve(
    import ('./components/Articulo.vue'));
//import Administrador from './components/Administrador.vue'
const Administrador = () => Promise.resolve(
    import ('./components/Administrador.vue'));
import Notfound from './components/Notfound.vue';

Vue.use(Router)
export default new Router({
    mode: 'history',
    routes: [{
            path: '/',
            component: Inicio,
            name: 'inicio',
        },
        {
            path: '/portada',
            redirect: { name: 'inicio' },
        },
        {
            path: '/inicio',
            redirect: { name: 'inicio' },
        },
        {
            path: '/home',
            redirect: { name: 'inicio' },
        },
        {
            path: '/sobremi',
            component: Sobremi,
            alias: ['/acerca'],
        },
        {
            path: '/contacto',
            component: Contacto,
            alias: ['/contactame'],
        },
        {
            path: '/post/:entrada',
            component: Post,
            children: [{
                path: '/articulo',
                component: Articulo,
                name: 'articulo',
            }, ]
        },

        {
            path: '/administrador/:admin',
            component: Administrador,
            props: true,
        },
        {
            path: '*',
            component: Notfound,
        },
    ]
})